.counters-block {
  margin-bottom: 50px;
  font-family: $title-font;
  font-size: 1.625rem;
  color: $title-color;
  @media (min-width: $tablet-width) {
    margin-bottom: 150px; }
  @media (min-width: $desktop-width) {
    margin-bottom: 250px; } }

.counters-block__layout {
  width: 80%;
  max-width: 450px;
  margin: 0 auto;
  @media (min-width: $tablet-width) {
    width: 100%;
    max-width: 875px;
    display: flex;
    justify-content: space-between; } }

.counters-block__item {
  margin-bottom: 35px;
  text-align: center;
  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    flex: 0 1 calc(100%/3 - 25px); } }

.counters-block__number {
  font-size: 3.75rem;
  text-align: center;
  margin-bottom: 15px;
  @media (min-width: $tablet-width) {
    margin-bottom: 10px; } }

.counters-block__text {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  line-height: 1.25;
  text-transform: uppercase; }
