.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); }


.video-modal {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 15;
  padding: 25px;
  width: 80%;
  border-radius: 10px;
  background: linear-gradient(to right, #6a3093, #a044ff);
  @media (min-width: $tablet-width) {
    width: 60%;
    padding: 40px; }
  @media (min-width: $desktop-width) {
    width: 650px;
    padding: 50px; } }

.video-modal__video {
  width: 100%;
  height: 45vh; }

.video-modal__close-btn {
  display: block;
  background-color: transparent;
  width: 25px;
  height: 25px;
  border: 1px solid #ff8b00;
  border-radius: 5px;
  margin-bottom: 5px;
  position: relative;
  cursor: pointer;
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 10px;
    left: -1px;
    width: 25px;
    height: 3px;
    background-color: #ff8b00; }
  &::before {
    transform: rotate(45deg); }
  &::after {
    transform: rotate(-45deg); }
  &:hover {
    border-color: #ffffff;
    &::before,
    &::after {
      background-color:#ffffff {} } } }

.hide {
  display: none; }
