.banner {
  background: linear-gradient(to right, #6a3093, #a044ff);
  @media (min-width: $tablet-width) {
    padding-top: 45px;
    padding-bottom: 40px;
    background: url('../img/banner-bg-tablet.jpg') no-repeat;
    background-size: cover; }
  @media (min-width: $desktop-width) {
    padding-top: 91px;
    padding-bottom: 77px;
    background: url('../img/banner-bg-desktop.jpg') no-repeat;
    background-size: cover; } }

.banner__layout {
  padding: 30px;
  display: flex;
  flex-direction: column;
  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    padding: 0; } }

.banner__title {
  margin: 0;
  padding: 0;
  align-self: flex-start;
  font-family: $title-font;
  font-size: 2.5rem;
  color: #ffffff;
  @media (min-width: $tablet-width) {
    align-self: center; } }

.banner__btn {
  align-self: flex-end;
  @media (min-width: $tablet-width) {
    align-self: center; } }
