.presentation {
  padding-top: 20px;
  padding-bottom: 35px;
  @media (min-width: $tablet-width) {
    margin: 0 -15px;
    padding-top: 50px;
    padding-bottom: 50px; }
  @media (min-width: $desktop-width) {
    margin: 0;
    margin-bottom: 120px;
    padding-top: 61px;
    padding-bottom: 0; } }

.presentation__layout {
  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  @media (min-width: $desktop-width) {
    align-items: flex-start; } }

.presentation__info {
  margin: {
    left: auto;
    top: 0;
    right: auto;
    bottom: 30px; }
  width: 75%;
  max-width: 470px;
  @media (min-width: $tablet-width) {
    margin: 0;
    flex: 0 1 calc(100%/2 - 40px); }
  @media (min-width: $desktop-width) {
    flex-basis: calc(100%/2 - 61px);
    padding-top: 102px; } }

.presentation__title {
  margin: 0;
  margin-bottom: 11px;
  padding: 0;
  text-align: center;
  font-family: $title-font;
  font-size: 2.5rem;
  color: $title-color;
  text-transform: capitalize;
  @media (min-width: $tablet-width) {
    text-align: left; } }

.presentation__text {
  margin: 0;
  margin-bottom: 33px;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1.46;
  text-align: center;
  @media (min-width: $tablet-width) {
    font-size: 0.937rem;
    text-align: left;
    padding-left: 5px; } }

.presentation__btn {
  margin: 0 auto;
  @media (min-width: $tablet-width) {
    margin: 0; } }

.presentation__video {
  margin: 0 -20px;
  min-height: 280px;
  padding-top: 80px;
  background: url('../img/video-bg-mobile.jpg') no-repeat center;
  background-size: cover;
  @media (min-width: $tablet-width) {
    min-height: 320px;
    padding-top: 100px;
    flex: 0 1 calc(100%/2 - 10px); }
  @media (min-width: $desktop-width) {
    min-height: 452px;
    padding-top: 170px;
    flex-basis: 724px;
    background: url('../img/video-bg.jpg') no-repeat center;
    background-size: cover; } }

.video__btn {
  display: block;
  position: relative;
  margin: {
    left: auto;
    top: 0;
    right: auto;
    bottom: 15px; }
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1px solid #ffffff;
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 35%;
    transform: translate(0, -50%);
    border: 10px solid transparent;
    border-left: 22px solid #ffffff; }
  @media (min-width: $desktop-width) {
    width: 82px;
    height: 82px;
    margin-bottom: 18px;
    &::before {
      left: 40%;
      border: 15px solid transparent;
      border-left: 27px solid #ffffff; } } }

.video__description {
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: "Raleway-Extrabold", sans-serif;
  font-size: 2.0625rem;
  line-height: 1.35;
  letter-spacing: 3.6px;
  color: #ffffff;
  text-transform: uppercase;
  .video__name {
    display: block;
    text-align: center;
    font-family: "Raleway-Regular", sans-serif;
    font-size: 1rem;
    letter-spacing: 1.8px; } }
