@font-face {
  font-family: "ProximaNova-Bold";
  src: url("../fonts/ProximaNova-Bold.woff") format("woff"),
  url("../fonts/ProximaNova-Bold.ttf") format("truetype") {}
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "ProximaNova-Semibold";
  src: url(../fonts/ProximaNova-Semibold.woff) format("woff"),
  url(../fonts/ProximaNova-Semibold.ttf) format("truetype") {}
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "ProximaNova-Regular";
  src: url(../fonts/ProximaNova-Regular.woff) format("woff"),
  url(../fonts/ProximaNova-Regular.ttf) format("truetype") {}
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway-Semibold";
  src: url(../fonts/Raleway-SemiBold.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway-Extrabold";
  src: url(../fonts/Raleway-ExtraBold.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway-Regular";
  src: url(../fonts/Raleway-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

$main-font: "ProximaNova-Regular", sans-serif;
$title-font: "ProximaNova-Bold",sans-serif;
$submain-font: "ProximaNova-Semibold", sans-serif;
$main-color: #878787;
$title-color: #7050e5;
$tablet-width: 768px;
$desktop-width: 1200px;
$mobile-width-only: 767px;

html {
  box-sizing: border-box;
  font-size: 12px;
  @media (min-width: $tablet-width) {
    font-size: 14px; }
  @media (min-width: $desktop-width) {
    font-size: 16px; } }


*,
*::before,
*::after {
  box-sizing: inherit; }

* {
  transition: all 0.5s ease; }

img {
  max-width: 100%;
  height: auto; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
  min-width: 320px;
  overflow-x: hidden;
  font-family: $main-font;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.1;
  color: $main-color; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  @media (min-width: $tablet-width) {
    padding: 0 25px; }
  @media (min-width: $desktop-width) {
    max-width: 1152px;
    padding: 0;
    &--wide {
      max-width: 1285px; } } }

.page-main {
  padding-top: 60px;
  overflow-x: hidden;
  @media (min-width: $tablet-width) {
    padding-top: 100px; }
  @media (min-width: $desktop-width) {
    padding-top: 135px; } }

.btn {
  display: block;
  width: 171px;
  padding: 1.2em 10px;
  background-color: #ff8b00;
  border: none;
  border-radius: 3px;
  text-align: center;
  font-family: $title-font;
  font-size: 0.875rem;
  color: #ffffff;
  line-height: 1.2;
  text-transform: uppercase;
  text-decoration: none;
  &:hover {
    background-color: darken(#ff8b00, 15%); } }

@import "components/header";
@import "components/header-preview";
@import "components/counters";
@import "components/services";
@import "components/presentation";
@import "components/banner";
@import "components/footer";
@import "components/modal";
