.page-header {
  min-height: 715px;
  font-family: $title-font;
  color: #ffffff;
  position: relative;
  padding-top: 15px;
  background: url('../img/header-bg-effect.png') no-repeat center;
  background-color:  #5f49cf;
  background-size: cover;
  background-blend-mode: multiply;
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    background: #5f49cf; }
  &::before,
  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    width: 0;
    height: 0;
    border-style: solid;
    z-index: 1; }
  &::before {
    left: 0;
    border-width: 50px 0 0 50vw;
    border-color: transparent transparent transparent #ffffff; }
  &::after {
    right: 0;
    border-width: 0 0 50px 50vw;
    border-color: transparent transparent #ffffff transparent; }
  @media (min-width: $tablet-width) {
    &::before {
      left: 0;
      border-width: 150px 0 0 50vw;
      border-color: transparent transparent transparent #ffffff; }
    &::after {
      right: 0;
      border-width: 0 0 150px 50vw;
      border-color: transparent transparent #ffffff transparent;
      padding-top: 32px; } }
  @media (min-width: $desktop-width) {
    min-height: 810px;
    padding-top: 30px;
    &::before {
      left: 0;
      border-width: 250px 0 0 50vw;
      border-color: transparent transparent transparent #ffffff; }
    &::after {
      right: 0;
      border-width: 0 0 250px 50vw;
      border-color: transparent transparent #ffffff transparent; } } }

.header-menu {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header-menu__logo {
  width: 50px;
  height: 72px;
  @media (min-width: $tablet-width) {
    width: 78px;
    height: 100px; } }

.main-nav {
  @media (max-width: $mobile-width-only) {
    position: fixed;
    top: 100px;
    right: 0;
    width: 70%;
    background-color: #5f49cf;
    opacity: 0.95;
    z-index: 10;
    transform: translateX(100%);
    &--opened {
      transform: translateX(0); } }
  @media (min-width: $tablet-width) {
    display: block;
    flex-basis: 500px;
    flex-shrink: 1; }
  @media (min-width: $desktop-width) {
    flex-basis: 570px;
    padding-top: 18px; } }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between; } }

.main-nav__link {
  display: block;
  position: relative;
  padding: 15px 0;
  text-align: center;
  border-bottom: 1px solid #ff8b00;
  font-size: 1.4rem;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  &:hover {
    color: lighten(#ff8b00, 35%); }
  &:active {
    color: lighten(#ff8b00, 55%); }
  @media (min-width: $tablet-width) {
    font-size: 0.875rem;
    border-bottom: none;
    padding: 0;
    &--active {
      &::before {
        content: '';
        position: absolute;
        left: -5px;
        right: -5px;
        bottom: -9px;
        height: 3px;
        background-color: #ff8b00; } } } }

.main-nav__item {
  &:last-of-type {
    .main-nav__link {
      border-bottom: none; } } }

.page-header__toggle {
  display: block;
  border: none;
  background-color: transparent;
  width: 20px;
  height: 20px;
  position: relative;
  cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    top: 1px;
    left: 0;
    width: 20px;
    height: 3px;
    background-color: #ffffff;
    box-shadow: 0 7px 0 0 #ffffff, 0 14px 0 0 #ffffff;
    border-radius: 3px; }
  &--opened {
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 9px;
      left: 4px;
      width: 20px;
      height: 3px;
      background-color: #ffffff; }
    &::before {
      transform: rotate(45deg);
      box-shadow: none; }
    &::after {
      transform: rotate(-45deg); } }
  @media (min-width: $tablet-width) {
    display: none; } }

