.services {
  padding-top: 30px;
  margin-bottom: 65px;
  background-color: #f8f8f8;
  @media (min-width: $tablet-width) {
    margin-bottom: 0; }
  @media (min-width: $desktop-width) {
    margin-bottom: 120px; } }

.services__title {
  position: relative;
  margin: 0;
  margin-bottom: 40px;
  text-align: right;
  padding: 0;
  font-family: $title-font;
  font-size: 3.75rem;
  text-transform: capitalize;
  color: $title-color;
  &::before,
  &::after {
    content: '';
    position: absolute;
    top: -15px;
    right: -15px;
    width: 25px;
    height: 8px;
    background-color: #ff8b00; }
  &::after {
    transform: rotate(90deg);
    top: -6px;
    right: -25px; }
  @media (min-width: $tablet-width) {
    padding-right: 75px;
    margin-bottom: 45px;
    &::before,
    &::after {
      width: 46px;
      height: 12px; }
    &::before {
      top: -25px;
      right: 30px; }
    &::after {
      top: -8px;
      right: 12px; } }
  @media (min-width: $desktop-width) {
    padding-right: 120px;
    &::before {
      top: -28px;
      right: 73px; }
    &::after {
      top: -11px;
      right: 56px; } } }

.services__list {
  margin: 0;
  padding: 0;
  list-style: none;
  @media (min-width: $tablet-width) {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around; }
  @media (min-width: $desktop-width) {
    max-width: 920px;
    justify-content: space-between; } }

.services__item {
  margin: {
    top: 0;
    bottom: 45px;
    left: auto;
    right: auto; }
  width: 85%;
  max-width: 450px;
  @media (min-width: $tablet-width) {
    flex: 0 1 calc(100%/2 - 40px);
    margin: 0;
    margin-bottom: 30px; }
  @media (min-width: $desktop-width) {
    flex: 0 0 285px; } }

.services__img-block {
  text-align: center;
  margin-bottom: 20px;
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    min-height: 170px;
    margin-bottom: 30px; } }

.services__name {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  text-align: center;
  font-family: $title-font;
  font-size: 1.0625rem;
  line-height: 1.2;
  color: $title-color;
  text-transform: uppercase;
  @media (min-width: $tablet-width) {
    text-align: left;
    margin-bottom: 5px; } }

.services__text {
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1.46;
  text-align: center;
  @media (min-width: $tablet-width) {
    font-size: 0.937rem; }
  @media (min-width: $desktop-width) {
    text-align: left; } }



